html{
	height: 100%;
}
body {
	background-image: url("mic.jpg");
	background-size: cover;
	background-repeat: no-repeat;
	height: 100%;
	display: flex;
  	justify-content: center;
  	overflow: hidden;
 }


#pad-display-container{
	width: 35vw;
	height: 60vh;
	display: grid;
	grid-template-columns: auto auto auto;
	grid-gap: 40px;
	padding: 10px;
	opacity: 0.5;
	margin-top: 20%;

}
#display{
	height: 1em;
	color: yellow;
	grid-column: 1/4;
	display: flex;
  	justify-content: center;
  	align-items: center;


}
.drum-pad{
	font-weight: bold;
	font-size: 2em;
	color: yellow;
	cursor: pointer;
	border: 5px solid yellow;
	border-radius: 20%;
	text-align: center;
	transition: 0.1s;
	display: flex;
  	justify-content: center;
  	align-items: center;

}
.active{
	transform: scale(1.04);
	color: #FF4040;
	border: 5px solid #FF4040;
	background-color: rgba(250, 249, 167, 0.4);
}

@media screen and (max-width: 600px){
	#pad-display-container{
		width: 70vw;
		min-height: 80vh;
	}
	#display{
		height: 1em;
	}
	.drum-pad{
		background-color: rgba(250, 249, 167, 0.4);
	}
}
